<template>
<label class="field__label" :for="inputId">{{ label }}</label>
	<input
		@keyup="handleChangeInput"
		:id="inputId"
		:type="inputType"
		v-model="value"
    class="field__input"
    :class="inputClass"
    :placeholder="inputPlaceHolder"
	/>
</template>

<script>
export default {
  name: 'Input',
  props: {
    label: String,
    inputClass: String,
    inputType: String,
    inputPlaceHolder: String,
    inputId: String
  },
  data() {
    return {
      value: ''
    }
  },
  methods: {
    handleChange() {
      this.$emit('handleChange', { name: this.name, value: this.value })
    }
  }
};
</script>

<style lang="scss" scoped></style>
